#navBox {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	font-size: 20px;
}

.navlinks {
	text-decoration: none;
	color: white;
	padding-left: 20px;
	padding-right: 20px;
	user-select: none;
}

.navlinks p {
	border-bottom: 2px solid transparent;
}

.navlinks:hover p {
	border-bottom: 2px solid white;
}

#navButton{
	background-color: white;
	width: 75px;
	height: 37.5px;
	position: absolute;
	top: 10px;
	left: 10px;
	border-radius: 10px;
	display: none;
	font-size: 22.5px;
	justify-content: center;
	align-items: center;
}

#navButton p{
	height: 30px;
	margin: 10px;
	line-height: 30px;
	text-align: center;
}

@media only screen and (max-width: 800px) {
	#navButton{
		z-index: 10;
		display: flex;
	}
	#navBox {
		padding-top: 50px;
		position: absolute;
		flex-flow: column;
		height: 100vh;
		width: 100vw;
		background-color: rgba(30, 30, 30, 0.7);
		display: none;
		z-index: 9;
		overflow: hidden;
	}
	.navlinks{
		padding: 0;
		padding-top: 10px;
		margin: 25px auto;
		width: 100%;
		text-align: center;
		font-size: 40px;
		display: flex;
		justify-content: center;
		align-items: center;
	}
	.navlinks p{
		margin: 0;
		padding: 0;
		width: fit-content;
	}
}